a {
  text-decoration: none;
  color: #E0ECFF;
}
* {
  box-sizing: border-box;
}
.token-box {
  border-top: 1px solid #203B60;
}
.token-box a {
  text-decoration: none;
}
.token-box .token-info {
  width: 760px;
  border: 1px solid #203B60;
  border-top: 0;
  margin: 0 auto;
}
.token-box .token-info .token-header {
  display: flex;
  padding: 0 24px;
  justify-content: space-between;
}
.token-box .token-info .token-header .token-header-left {
  width: 444px;
}
.token-box .token-info .token-header .token-header-left .logo-title {
  height: 48px;
  display: flex;
  align-items: center;
  margin-top: 24px;
}
.token-box .token-info .token-header .token-header-left .logo-title .title-text {
  margin-left: 24px;
  font-size: 32px;
  color: #FFFFFF;
  font-weight: normal;
}
.token-box .token-info .token-header .token-header-left .token-header-des {
  margin-top: 14px;
  font-weight: normal;
}
.token-box .token-info .token-header .token-header-right {
  width: 242px;
  margin-top: 48px;
}
.token-box .token-info .token-header .learn-more {
  color: #CD7A37;
  text-decoration: none;
}
.token-box .token-info .token-info-content {
  width: 758px;
  display: flex;
  border-top: 1px solid #203B60;
  border-bottom: 1px solid #203B60;
  border-radius: 4px;
  margin-top: 73px;
  overflow: hidden;
}
.token-box .token-info .token-info-content .top-info-box,
.token-box .token-info .token-info-content .buttom-info-box {
  display: flex;
}
.token-box .token-info .token-info-content .token-burning {
  width: 50%;
}
.token-box .token-info .token-info-content .token-burning .info-box {
  width: 100%;
}
.token-box .token-info .token-info-content .info-box {
  height: 136px;
  border-right: 1px solid #203B60;
  background: rgba(85, 119, 253, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.token-box .token-info .token-info-content .info-box .num {
  font-size: 24px;
  color: #E0ECFF;
  height: 45px;
  display: flex;
  align-items: center;
}
.token-box .token-info .token-info-content .info-box .text {
  font-size: 12px;
}
.token-box .token-info .token-info-content .info-box:nth-child(4) {
  border: 0;
}
.token-box .token-info .token-burning-content {
  margin-top: 37px;
}
.token-box .token-info .token-burning-content .buttom-info-box .info-box {
  border-right: 0;
}
.token-box .token-info .buy-deri-token,
.token-box .token-info .benefits-title,
.token-box .token-info .faq-title {
  text-align: center;
  font-size: 18px;
  color: #E0ECFF;
  margin-top: 61px;
}
.token-box .token-info .buy-deri-token-swap {
  display: flex;
  margin-top: 37px;
  overflow: hidden;
}
.token-box .token-info .buy-deri-token-swap .top-box {
  display: flex;
  justify-content: space-between;
}
.token-box .token-info .buy-deri-token-swap .swap-box {
  height: 100px;
  background: rgba(85, 119, 253, 0.1);
  border: 1px solid #203B60;
  box-sizing: border-box;
  border-radius: 4px;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: center;
  flex-direction: column;
}
.token-box .token-info .buy-deri-token-swap .swap-box .swap-name {
  font-size: 16px;
  color: #E0ECFF;
}
.token-box .token-info .buy-deri-token-swap .swap-box img {
  width: 24px;
  height: 24px;
}
.token-box .token-info .buy-deri-token-swap .swap-box:hover {
  border: 1px solid #CD7A37;
}
.token-box .token-info .benefits-faq-box {
  margin-bottom: 48px;
}
.token-box .token-info .benefits-faq-box .benefits-box {
  border-bottom: 1px solid #203B60;
  margin-bottom: 48px;
}
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info {
  width: 675px;
  height: 90px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 48px;
  margin-top: 24px;
}
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege {
  width: 303px;
  padding: 0 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .governance-des,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .governance-des,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .privilege-des,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .privilege-des {
  margin-left: 16px;
}
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .governance-des .governance-title,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .governance-des .governance-title,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .privilege-des .governance-title,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .privilege-des .governance-title,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .governance-des .privilege-title,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .governance-des .privilege-title,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .privilege-des .privilege-title,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .privilege-des .privilege-title {
  font-weight: 600;
  font-size: 18px;
  color: #F3F4F8;
}
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .governance-des .governance-text,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .governance-des .governance-text,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .privilege-des .governance-text,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .privilege-des .governance-text,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .governance-des .privilege-text,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .governance-des .privilege-text,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .governance .privilege-des .privilege-text,
.token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege .privilege-des .privilege-text {
  font-weight: 400;
  font-size: 14px;
  margin-top: 16px;
}
.token-box .token-info .benefits-faq-box .faq-box .faq-info {
  margin-top: 35px;
  border: 1px solid #203B60;
  border-bottom: 0;
  background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
}
.token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box {
  min-height: 48px;
  border-bottom: 1px solid #203B60;
}
.token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-title-img {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 48px;
  padding-left: 48px;
  padding-right: 24px;
  cursor: pointer;
}
.token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-des {
  font-size: 14px;
  padding-left: 48px;
  padding-right: 24px;
  padding-bottom: 25px;
  font-weight: normal;
}
.token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-des .governance-privilege {
  color: #5577FD;
}
.token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-des .privilege,
.token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-des .specifically,
.token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-des .specifically-des {
  margin-top: 16px;
}
.token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-des .specifically-des {
  display: flex;
}
.token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-des .specifically-des .list-style {
  background-color: #5577FD;
  min-width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-top: 4px;
  margin-right: 4px;
}
@media only screen and (width: 760px) {
  .token-box .token-info {
    border: 0;
  }
}
@media only screen and (max-width: 759px) {
  .token-box .token-info {
    width: 100%;
    height: 100%;
    min-width: 375px;
    border: 0;
  }
  .token-box .token-info .token-header {
    display: block;
  }
  .token-box .token-info .token-header .token-header-left {
    width: 100%;
  }
  .token-box .token-info .token-header .token-header-right {
    margin-top: 42px;
  }
  .token-box .token-info .token-info-content {
    width: 100%;
    margin-top: 24px;
    padding: 0 24px;
    display: block;
    border: 0;
  }
  .token-box .token-info .token-info-content .top-info-box,
  .token-box .token-info .token-info-content .buttom-info-box {
    width: 100%;
    justify-content: space-between;
  }
  .token-box .token-info .token-info-content .top-info-box .info-box,
  .token-box .token-info .token-info-content .buttom-info-box .info-box {
    width: 50%;
  }
  .token-box .token-info .token-info-content .top-info-box .min-box,
  .token-box .token-info .token-info-content .buttom-info-box .min-box {
    border-right: 0;
  }
  .token-box .token-info .token-info-content .token-burning .info-box {
    width: 100%;
    border-right: 0;
  }
  .token-box .token-info .token-info-content .top-info-box {
    border-bottom: 1px solid #203B60;
  }
  .token-box .token-info .buy-deri-token-swap {
    width: 100%;
    flex-wrap: wrap;
    padding: 0 24px;
  }
  .token-box .token-info .buy-deri-token-swap a {
    flex: 1;
    margin: 0 16px 16px 0;
    width: calc((100% - 32px) / 2);
    min-width: calc((100% - 32px) / 2);
    max-width: calc((100% - 32px) / 2);
  }
  .token-box .token-info .buy-deri-token-swap .swap-box {
    width: 100%;
  }
  .token-box .token-info .benefits-faq-box .benefits-box .benefits-info {
    width: 100%;
    min-width: 375px;
    flex-wrap: wrap;
    height: 100%;
  }
  .token-box .token-info .benefits-faq-box .benefits-box .benefits-info .privilege {
    margin-top: 24px;
  }
  .token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-title-img,
  .token-box .token-info .benefits-faq-box .faq-box .faq-info .faq-info-box .faq-des {
    padding-left: 24px;
  }
}
@media only screen and (max-width: 375px) {
  width: 100%;
  flex-wrap: wrap;
  padding: 0 24px;
  a {
    flex: 1;
    margin: 0 16px 16px 0;
    width: 156px;
  }
  .swap-box {
    width: 100%;
  }
}
@media only screen and (min-width: 760px) {
  .token-box .token-info {
    height: 100%;
  }
  .token-box .token-info .token-header {
    height: 124px;
  }
  .token-box .token-info-content {
    height: 136px;
  }
  .token-box .token-info-content .info-box {
    width: 190px;
  }
  .token-box .buy-deri-token-swap {
    width: 759px;
    justify-content: space-between;
  }
  .token-box .buy-deri-token-swap .swap-box {
    width: 184px;
  }
}
